function V_ui_generateSvgIcon(i, w, h, c, s) {
    s = (s !== gUndefined) ? ' style="' + s + '"' : ""
    return '<svg width="' + w + 'px" height="' + h + 'px"' + s + ' class="v-svg-small-ico">'
        + '<use class="v-svg-ico-' + c + '" xlink:href="#' + i + '">'
        + '</use></svg>';
}

